import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    products : [
      {
        itemId: 1,
        image: 'https://raw.githubusercontent.com/gwenf/vuetify-responsive/master/public/img/products/product-1.jpg',
        name: 'Nintendo Switch',
        price: 299.99
      },
      {
        itemId: 2,
        image: 'https://raw.githubusercontent.com/gwenf/vuetify-responsive/master/public/img/products/product-2.jpg',
        name: 'Gameboy Mug',
        price: 19.99
      },
      {
        itemId: 3,
        image: 'https://raw.githubusercontent.com/gwenf/vuetify-responsive/master/public/img/products/product-3.jpg',
        name: 'Family Computer',
        price: 70
      },
      {
        itemId: 4,
        image: 'https://raw.githubusercontent.com/gwenf/vuetify-responsive/master/public/img/products/product-4.jpg',
        name: 'Jeopardy Game',
        price: 30
      },
      {
        itemId: 5,
        image: 'https://raw.githubusercontent.com/gwenf/vuetify-responsive/master/public/img/products/product-5.jpg',
        name: 'Game Cube',
        price: 99.99
      },
      {
        itemId: 6,
        image: 'https://raw.githubusercontent.com/gwenf/vuetify-responsive/master/public/img/products/product-6.jpg',
        name: 'Gameboy Color',
        price: 45
      }
    ],
    priceFilter: 'Any',
    snackbar: {
      show: false,
      variant: 'success',
      message: 'Success! Item added to the cart.'
    },
    cart: [] 
  },
  getters: {
    priceFilter: state => {
      return state.priceFilter;
    },
    cartItems: state => {
      return state.cart;
    },
    cartTotalPrice: state => {
      let total = 0;
      state.cart.forEach(element => {
        total += element.item.price * element.quantity
      });
      return total;
    }
  },
  mutations: {
    addItemToCart(state, payload) {
      const { item, quantity } = payload
      const idx = state.cart.findIndex((product) => {
        return product.item.itemId === item.itemId
      })
      if (idx === -1) {
        state.cart.push({ item, quantity })
      } else {
        state.cart[idx].quantity += 1
      }
    },
    updateSnackbar(state, settings) {
      state.snackbar = {
        ...state.snackbar,
        ...settings
      }
    },
    updatepriceFilter(state, priceFilter) {
      state.priceFilter = priceFilter;
    },
    updateCart(state, itemId) {
      const idx = state.cart.findIndex((product) => {
        return product.item.itemId === itemId
      })

      if (idx !== -1)
        state.cart.splice(idx, 1);
    },
    updateCartQuantity(state, payload) {
      const { value, index } = payload;
      //console.log(value, index);
      state.cart[index].quantity = value;
    }
  },
  actions: {
    updatepriceFilter({commit}, payload) {
      commit('updatepriceFilter', payload);
    },
    updateCart({commit}, payload) {
      commit('updateCart', payload);
    },
    updateCartQuantity({commit}, payload) { 
      commit('updateCartQuantity', payload);
    }
  }
})
