<template>
  <div>
    <v-row v-for="(cartItem, index) in cartItems" :key="cartItem.item.name">
      <HorizontalProduct :ItemId="cartItem.item.itemId" :index="index" />
    </v-row>
  </div>
</template>

<script>
import HorizontalProduct from "../cards/HorizontalProduct";

export default {
  components: {
    HorizontalProduct,
  },
  methods: {},
  computed: {
    cartItems: {
      get() {
        console.log("get");
        return this.$store.state.cart;
      },
      set(value) {
        console.log("set");
        this.$store.commit("updateCart2", value);
      },
    },
  },
};
</script>