<template>
  <div>
    <h3>Products</h3>

    <v-row>
      <v-col
        sm="6"
        md="4"
        v-for="filteredProduct in filteredProducts"
        :key="filteredProduct.name"
      >
        <VerticalProduct :product="filteredProduct" :addToCart="addToCart" />
      </v-col>
    </v-row>
  </div>
</template>

<script>
import VerticalProduct from "../products/VerticalProduct";

export default {
  components: {
    VerticalProduct,
  },
  methods: {
    addToCart(item, quantity = 1) {
      this.$store.commit("addItemToCart", { item, quantity });
      this.$store.commit("updateSnackbar", { show: true });
    },
  },
  data() {
    return {};
  },
  computed: {
    priceFilter: {
      get() {
        return this.$store.getters.priceFilter;
      },
    },
    filteredProducts() {
      return this.$store.state.products.filter((product) => {
        switch (this.priceFilter) {
          case "Any":
            console.log("any");
            return product;
          case "Under $25":
            console.log("under 25");
            return product.price < 25;
          case "$25 to $100":
            console.log("$25 to $100");
            return product.price >= 25 && product.price < 100;
          case "$100 to $500":
            console.log("$100 to $500");
            return product.price >= 100 && product.price <= 500;
          case "Over $500":
            console.log("Over $500");
            return product.price >= 500;
        }
      });
    },
  },
};
</script>