<template>
  <div>
    <v-sheet v-if="$vuetify.breakpoint.mdAndUp" elevation="4" class="pa-8">
      Price
      <v-radio-group v-model="priceFilter" :mandatory="true">
        <v-radio v-for="(n, i) in prices" :key="i" :label="`${n}`" :value="n">
        </v-radio>
      </v-radio-group>
    </v-sheet>
    <v-expansion-panels v-else>
      <v-expansion-panel>
        <v-expansion-panel-header> Filters </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-radio-group v-model="priceFilter" :mandatory="true">
            <v-radio
              v-for="(price, i) in prices"
              :key="i"
              :label="price"
              :value="price"
            >
            </v-radio>
          </v-radio-group>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //,
      prices: ["Any", "Under $25", "$25 to $100", "$100 to $500", "Over $500"],
      checkbox: true,
    };
  },
  computed: {
    priceFilter: {
      get() {
        return this.$store.getters.priceFilter;
      },
      set(priceFilter) {
        this.$store.dispatch("updatepriceFilter", priceFilter);
      },
    },
  },
  methods: {},
};
</script>